
.photo {
  width: 100%;
  height: 100px;
  background-color: #35495E;
  padding-top: 40px;
}
.white {
  color: white;
}
.photo img {
  display: block;
  /* margin: 0 auto; */
  margin-left: 40px;
  width: 80px;
  float: left;
  /* height: 100px; */
}
.login-div {
  height: 100%;
}
.login-info {
  margin-top: 20px;
  float: left;
  color: white;
  margin-left: 20px;
}
.vux-demo-header-box1 {
  z-index: 100;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
}
.header-icon {
  position: absolute;
  top: 0;
  z-index: 999;
  line-height: 50px;
  height: 100%;
  color: white;
}
.header-left-icon {
  margin-left: 10px;
  left: 0;
}
.header-right-icon {
  margin-right: 10px;
  right: 0;
}



