
.groupCourses .vux-tab .vux-tab-item{
    line-height: 20px;
}
.groupCourses .pickDate
  {
    color:#DD5858;
    font-size: 16px;
}
.groupCourses .classItems{
     margin-top: 145px;
}
.groupCourses .left-calendar{
    margin-left:5px;
    border-radius: 5px;
    background: #eee;
}
.groupCourses .vux-tab{
    height: 46px
}
.groupCourses .activeItem img{
    width: 100%
}
.groupCourses .activeItem{
  margin:10px 10px 0 10px;
  position: relative;
  height: 180px;
  overflow: hidden;
  border-radius: 5px
}
.groupCourses .topDate{
   position: absolute;
   top: 52px;
   z-index: 10;
   width: 100%;
}
.groupCourses .activeItem .sbottom{
  color: white;
  position: absolute;
  bottom: 10px;
  padding:10px;
  width: 85%
}
.groupCourses .activeItem .title{
  font-size: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.groupCourses .time-line{
  font-size: 14px;
  width: 100%;
  height: 20px;
}
.groupCourses .activeItem .view{
  float: right;
  font-size: 14px;
}
.groupCourses .activeItem img{
    width: 100%
}
.groupCourses .vux-tab .vux-tab-item.vux-tab-disabled {
    color: #666;
}
.groupCourses .vux-tab .vux-tab-item.vux-tab-selected{
  color:#DD5858;
  border-width: 3px !important;
  border-bottom: 3px solid #DD5858;
}
.groupCourses .bookBtn{
    position: absolute;
    bottom: 20px;
    right: 20px;
}
.groupCourses button.weui-btn_mini{
    width: 65px;
    padding: 0;
}
.groupCourses .selectGroup{
  background: #fff
}
.groupCourses .group-head{
    padding: 10px 0 5px 0;
    font-size: 14px;
    border-bottom: 1px solid #eee
}
.groupCourses .purpose-icon, .groupCourses .category-icon{
  text-align: right;
  padding-right: 10px
}
.groupCourses .purpose-icon img{
    height:20px;
}
.groupCourses .arrow-icon{
  text-align: right;
  padding-right: 15px;
}
.groupCourses .group-items{
    padding:0 30px;
    border-bottom: 1px solid #eee
}
.groupCourses .group-items p{
      overflow: hidden;
      text-align: center;
      font-size: 14px;
      height: 30px;
      line-height:30px;
      color: #666;
}
.groupCourses .vux-tab .vux-tab-item.vux-tab-notSelected{
      border-bottom:1px solid #eee;
      color: #666
}
.groupCourses .inline-calendar td.current > span.vux-calendar-each-date {
    background-color: #DD5858;
}
.groupCourses .inline-calendar td.is-today{
    color:#DD5858;
}
.groupCourses .qtyProgress{
    width: 70px;
    position: absolute;
    top: 10px;
    left: 10px;
    border-radius: 10px;
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    text-align: center;
    background: #666;
    color: #fff
}
.groupCourses .qbar{
    border-radius: 10px;
    position: absolute;
    height: 100%;
    top: 0
}
.groupCourses .qty{
    text-align: center;
    width: 100%;
    border-radius: 10px;
    position: absolute;
    height: 100%;
    top: 0
}
.groupCourses .d-box .d-title{
      margin-bottom:15px;
}
.groupCourses .d-box  .weui-dialog{
     max-width: 240px;
}
.groupCourses .d-icon{
      text-align: center;
      padding:10px;
}
.groupCourses .d-icon img{
      height: 70px;
      margin:15px auto;
}




