
.valiCodeBtn {
    font-size: 14px;
    text-align: center;
    line-height: 26px;
    border-radius: 3px;
    width: 90px;
    background-color: #4F81BD;
    color: white;
    position: absolute;
    /* top: 40px; */
    right: 30px;
    top: 10px;
}
.mine {
    overflow-x: hidden;
}
.white {
  color: white;
}
.vux-demo-header-box1 {
  z-index: 100;
  position: absolute;
  width: 100%;
  /* left: 0; */
  /* top: 0; */
}
.header-icon {
  position: absolute;
  top: 0;
  z-index: 999;
  line-height: 50px;
  height: 100%;
  color: white;
}
.header-left-icon {
  margin-left: 10px;
  left: 0;
}
.header-right-icon {
  margin-right: 10px;
  right: 0;
}



