
.activityIndex .activeItem{
  margin:10px 10px 0 10px;
  position: relative;
  height: 180px;
  overflow: hidden;
  border-radius: 5px;
  background-color: lightblue;
}
.activityIndex .activeItem .sbottom{
    /* color: white; */
    position: absolute;
    bottom: 10px;
    padding: 10px;
    width: 85%;
}
.activityIndex .activeItem .title{
  font-size: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.activityIndex .time-line{
  font-size: 14px;
  width: 100%;
  height: 20px;
}
.activityIndex .activeItem .view{
  float: right;
  font-size: 14px;
}
.activityIndex .activeItem .newsImg img{
    /* width: 100%; */
    width: 100px;
    min-height: 100px;
}
.activityIndex  .sbottom img{
       width: 16px;
    height: 12px;
    padding-right: 5px;
}



